@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-size: 16px;
	font-family: "Oswald", sans-serif;
}

body::-webkit-scrollbar{
	width: 0;
}

.container {
	display: flex;
	flex-direction: column;
	width: 100vw;
	max-width: 100vw;
	min-height: 100vh;
	overflow: hidden;
}

.container header.main-header {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: rgb(37, 37, 37);
	min-height: 80px;
}

.container header.main-header .bg-title {
	background-color: red;
	padding: 5px;
	height: 65px;
	width: 140px;
}

.container header.main-header h1 {
	display: flex;
	font-size: 2rem;
	justify-content: center;
	color: white;
	text-transform: uppercase;
	letter-spacing: -3px;
	font-weight: 700;
	text-shadow: 0px 0px 10px rgb(0, 0, 0);
	transform: scale(1.5, 2) translateX(-2.5px);
}

nav.navigation {
	display: flex;
	justify-content: center;
	padding: 10px;
	width: 100%;
	border-top: 0.5px solid rgba(255, 255, 255, 0.349);
	flex-wrap: wrap;
	box-shadow: 0px 10px 10px 0px grey;
}

nav address {
	margin: 0 10px;
}

nav address a {
	color: rgb(182, 182, 182);
	text-transform: capitalize;
	text-decoration: none;
	font-weight: 300;
	font-style: normal;
	padding-bottom: 8px;
	font-size: 1rem;
}

nav address a:hover {
	color: rgb(255, 255, 255);
	border-bottom: 2px solid red;
}

main {
	display: flex;
	position: relative;
	overflow: hidden;
	justify-content: center;
	/* align-items: center;? */
}

main .background {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-image: url("https://developer.marvel.com/i/images/developers/account-bg.jpg");
	background-position: center;
	background-size: cover;
	opacity: 0.3;
	z-index: -1;
	max-width: 100vw;
}

section {
	display: flex;
	position: relative;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	width: 100vw;
	height: auto;
}

@media screen and (min-width: 650px) {
	nav address a {
		font-size: 1.3rem;
	}
	main .background {
		background-size: contain;
		background-repeat: no-repeat;
	}
}

@media screen and (min-width: 1450px) {
	section {
		width: 80vw;
		height: auto;
	}
}
