.card {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	margin-top: 30px;
	width: 100%;
	min-width: 300px;
}
.card figure {
	margin-bottom: 10px;
	width: 200px;
	height: 300px;
	overflow: hidden;
	box-shadow: 0px 0px 10px 5px rgba(128, 128, 128, 0.596);
}

.card figure img {
	transition: 0.3s;
	width: 100%;
	height: 100%;
}
.card figure img:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	box-shadow: 0px 0px 10px 5px rgba(255, 0, 0, 0.596);
}
.card figure:hover {
	cursor: pointer;
	border: 2px solid red;
}

.card header {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 90%;
	height: 70px;
}

.card header h1 {
	text-align: center;
}

