fieldset.fielset-for-search {
	position: fixed;
	padding: 10px;
	border: none;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	overflow: hidden;
	width: 310px;
   top: 110px;
   right: 10px;
   z-index: 50;
}

fieldset.fielset-for-search .input-box {
	width: 95%;
	overflow: hidden;
}

fieldset.fielset-for-search .complete-input-box {
	transition: 0.3s;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	border: none;
	height: 45px;
	width: 100%;
}

fieldset.fielset-for-search .rounded {
	transition: 0;
	padding: 15px 20px;
	border-radius: 50% 0 0 50%;
	border: none;
	background-color: rgb(228, 227, 227);
}

fieldset.fielset-for-search input {
	width: 100%;
	padding: 15px 20px;
	border: none;
	background-color: rgb(228, 227, 227);
   -webkit-transform: translateX(-23px);
           transform: translateX(-23px);
}
fieldset.fielset-for-search input:focus {
	border: none;
	outline: none;
}

fieldset.fielset-for-search button {
	position: absolute;
   width: 56px;
   height: 56px;
   border-radius: 50%;
	border: none;
	right: 5px;
   font-weight: 900;
   transition: .3s;
}


fieldset.fielset-for-search button:hover {
	cursor: pointer;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

fieldset.fielset-for-search .hide {
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}


